.header {
  padding: 47px 0;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 61px;
}

.header__nav {
  display: flex;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 91px;
}

.nav__link {
  font-family: 'FuturaPT', sans-serif;
  color: #fff;
  font-size: 18px;
  line-height: 23px;
  transition: color .3s ease-in-out;
}

.nav__link:hover {
  color: #FFD600;
}

.burger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: auto;
  width: 24px;
  height: 16px;
}

.burger__line {
  display: block;
  width: 100%;
  height: 1px;
  background-color: #fff;
}

.hero__container {
  height: 100%;
  z-index: 200;
}

.hero__title {
  margin: 0;
  font-family: 'Fellaz', sans-serif;
  font-weight: 700;
  font-size: 196px;
  line-height: 275px;
  text-align: right;
  text-transform: uppercase;
  background-image: url(../img/bg.gif);
  background-repeat: no-repeat;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.blockquote {
  padding-top: 130px;
  padding-bottom: 78px;
}

.blockquote__container {
  background-image: url(../img/arrow_bg.svg);
  background-repeat: no-repeat;
  background-position: top right;
}

.blockquote__text {
  position: relative;
  padding-left: 237px;
  margin-right: 370px;
  font-family: 'Fellaz', sans-serif;
  font-weight: 400;
  font-size: 48px;
  line-height: 69px;
  color: #FFD600;
}

.blockquote__text::after {
  content: '';
  width: 123px;
  height: 72px;
  position: absolute;
  left: 0;
  top: 0;
  background-image: url(../img/blockquote.svg);
}

.blockquote__text::before {
  content: '';
  width: 2px;
  height: 80%;
  position: absolute;
  left: 200px;
  top: 50%;
  transform: translateY(-50%);
  border-left: 1px solid rgba(255, 214, 0, 0.3);
}

.education__container {
  display: grid;
  grid-template-columns: 472px 472px 472px;
  grid-template-rows: 463px 266px;
}

.education__link {
  padding: 40px 30px;
  height: 100%;
  transition: background-image .3s ease-in-out;
}

.education__link-num {
  display: block;
  font-family: 'Fellaz', sans-serif;
  font-weight: 400;
  font-size: 72px;
  line-height: 103px;
  letter-spacing: -0.025em;
  color: #fff;
}

.education__link-text {
  font-family: 'Fellaz', sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 52px;
  color: #fff;
}

.education .card_1 {
  grid-row: 1/3;
  background-image: url(../img/education_bg-1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.education .card_1:hover {
  background-image: url(../img/01.gif);
}

.education .card_2 {
  grid-row: 1/3;
  background-image: url(../img/education_bg-2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.education .card_2:hover {
  background-image: url(../img/01.gif);
}

.education .card_3 {
  background-image: url(../img/education_bg-3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.education .card_3:hover {
  background-image: url(../img/01.gif);
}

.education .card_4 {
  background-image: url(../img/education_bg-4.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.education .card_4:hover {
  background-image: url(../img/01.gif);
}

.about {
  padding-top: 96px;
  padding-bottom: 50px;
}

.about__title {
  margin-bottom: 55px;
}

.about__cards {
  display: flex;
  justify-content: space-between;
  margin-bottom: 53px;
}

.about__card-bg {
  margin-bottom: 20px;
  width: 317px;
  height: 220px;
  background-repeat: no-repeat;
  background-size: cover;
}

.about__card-num {
  padding: 48px 0;
  display: block;
  font-family: 'FuturaPT', sans-serif;
  font-weight: 600;
  font-size: 96px;
  line-height: 123px;
  color: #fff;
  text-align: center;
}

.about__card-text {
  max-width: 90%;
  font-family: 'FuturaPT', sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 31px;
  color: #fff;
}

.about .card-1 {
  background-image: url(../img/about_bg-1.jpg);
}

.about .card-2 {
  background-image: url(../img/about_bg-2.jpg);
}

.about .card-3 {
  background-image: url(../img/about_bg-3.jpg);
}

.about .card-4 {
  background-image: url(../img/about_bg-4.jpg);
}

.about__text {
  margin-bottom: 23px;
  font-family: 'Fellaz', sans-serif;
  font-weight: 400;
  font-size: 48px;
  line-height: 69px;
  color: #fff;
}

.about__text-orange {
  max-width: 50%;
  color: #FE6927;
}

.about__text-anakiwa {
  color: #71E5FF;
}

.about__text-saffron {
  color: #F4B73E;
}

.about__col {
  margin-left: 50%;
}

.about__photo {
  margin-bottom: 100px;
}

.about__col-top,
.about__col-bottom {
  position: relative;
}

.about .photo-3 {
  position: absolute;
  top: 57px;
  left: -466px;
}

.about .photo-4 {
  position: absolute;
  top: -200px;
  left: -710px;
}

.teachers {
  padding-bottom: 100px;
}

.teachers__title {
  margin-bottom: 46px;
}

.teachers__photo {
  margin-bottom: 10px;
  width: 240px;
  height: 230px;
  object-fit: cover;
}

.teachers__name {
  font-family: 'Fellaz', sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 29px;
  color: #fff;
}

.teachers__position {
  font-family: 'FuturaPT', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #fff;
}

.teachers .swiper-slide {
  padding: 20px 28px;
  height: 380px;
  background-repeat: no-repeat;
  background-size: cover;
}

.teachers .slide-1 {
  background-image: url(../img/slide-1_bg.jpg);
}

.teachers .slide-2 {
  background-image: url(../img/slide-2_bg.jpg);
}

.teachers .slide-3 {
  background-image: url(../img/slide-3_bg.jpg);
}

.teachers .slide-4 {
  background-image: url(../img/slide-4_bg.jpg);
}

.teachers .slide-5 {
  background-image: url(../img/slide-5_bg.jpg);
}

.teachers .slide-6 {
  background-image: url(../img/slide-6_bg.jpg);
}

.teachers .swiper-container {
  margin-bottom: 100px;
}

.teachers .university-descr {
  max-width: 50%;
  font-family: 'FuturaPT';
  font-weight: 400;
  font-size: 36px;
  line-height: 46px;
  color: #fff;
}

.university {
  display: flex;
  margin-bottom: 98px;
}

.university__title {
  margin-right: 45px;
  font-family: 'Fellaz', sans-serif;
  font-weight: normal;
  font-size: 98px;
  background-image: url(../img/title-bg.jpg);
  background-repeat: no-repeat;
  background-size: 120%;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.university__title span {
  display: block;
  font-size: 149px;
  line-height: 169px;
}

.university__subtitle {
  padding-top: 20px;
  font-family: 'FuturaPT';
  font-weight: 400;
  font-size: 36px;
  line-height: 46px;
  color: #fff;
}

.footer {
  padding-bottom: 90px;
}

.footer__container {
  padding-top: 50px;
  border-top: 6px solid rgba(255, 255, 255, 0.3);
}

.footer__top {
  display: flex;
  margin-bottom: 100px;
}

.footer__logo {
  margin-bottom: 20px;
}

.footer__info {
  margin-right: 61px;
  max-width: 175px;
  display: flex;
  flex-direction: column;
}

.footer__info-text {
  margin-bottom: 10px;
  font-family: 'FuturaPT', sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #fff;
}

.footer__info-tel {
  font-family: 'FuturaPT', sans-serif;
  font-size: 18px;
  line-height: 23px;
  color: #fff;
}

.footer__bottom {
  display: flex;
}

.footer__bottom-text {
  font-family: 'FuturaPT', sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #fff;
}

.footer__col-left {
  max-width: 236px;
  margin-right: auto;
}

.footer__col-right {
  max-width: 581px;
}

.footer-nav {
  display: flex;
  align-items: center;
}

.footer-nav__list:not(:last-child) {
  margin-right: 121px;
}

.footer-nav__item:not(:last-child) {
  margin-bottom: 30px;
}

.footer-nav__link {
  font-family: 'FuturaPT', sans-serif;
  font-size: 18px;
  line-height: 23px;
  color: #fff;
}